@media screen and (min-width:1920px) {
    body {
        section {

            p {
                font-size: 1rem !important;
            }

            .container {
                width: 60% !important;
            }

            .banner_parent {
                .banner {
                    .banner_content {
                        height: 65vh;
                    }

                    .down_arrow {
                        width: 65px;
                        height: 65px;
                        margin-top: 60px;
                    }
                }
            }
        }
    }
}

@media (max-width:1500px) {
    body {
        section {
            .program_parent {
                margin-top: 80px;
            }
        }
    }
}

@media screen and (max-width:1367px) {
    body {

        section {

            .container {
                width: 75% !important;
            }

            .banner_parent {
                .banner {
                    .banner_content {
                        width: max(305px, 40%);
                        height: max(550px, 55vh);

                        .content {
                            width: 80%;

                            h1 {
                                font-size: clamp(.8rem, 2.5vw, 3rem);
                                width: 200px;
                            }

                            h4 {
                                margin-bottom: 10px;
                            }

                            button {
                                padding: 15px 0;
                                font-size: .9rem;
                                margin-top: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:1025px) {
    body {
        p {
            font-size: .9rem;
        }

        section {

            .container {
                width: 85% !important;
            }

            .banner_parent {
                .banner {
                    .banner_content {
                        width: max(400px, 40%);
                        height: max(420px, 60dvh);

                        .content {
                            width: 75%;

                            .logo {
                                width: max(200px, 50%)
                            }

                            h1 {
                                font-size: clamp(.8rem, 2.5vw, 3rem);
                                width: 200px;
                            }

                            p {
                                font-size: .7rem;
                            }

                            h4 {
                                margin-bottom: 10px;
                            }
                            a{
                                button {
                                    padding: 15px 0;
                                    font-size: .9rem;
                                    margin-top: 10px;
                                }
                            }
                            
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:500px) {
    $main-spacing: 50px;

    body {
        section {

            .container {
                width: 90% !important;
            }

            h1 {
                font-weight: bold !important;
            }

            //Banner styling begins here...
            .banner_parent {
                .banner {
                    background-size: 200%;

                    .banner_content {
                        width: max(340px, 90%);
                        height: max(470px, 55dvh);

                        .content {
                            width: 80%;

                            .logo {
                                width: max(250px, 50%)
                            }

                            h1 {
                                font-size: clamp(.8rem, 8vw, 8rem);
                            }

                            h3 {
                                font-size: clamp(1.3rem, 2vw, 2rem);
                            }
                        }
                    }
                }
            }

            //Program outline styling begins here...
            .program_parent {
                margin-top: $main-spacing;

                .container {

                    h1 {
                        font-size: clamp(1.8rem, 2.5vw, 2.5rem);
                    }

                    .program_container {
                        .header {
                            font-size: clamp(1.5rem, 2vw, 2rem);
                        }

                        .program_body {
                            padding: 50px 30px 100px;

                            .program_calender {
                                .program_flexbox {
                                    .flex:first-child {
                                        display: none;
                                    }

                                    .flex {
                                        .mobile_time {
                                            display: block;
                                            font-weight: bold;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            //Accomodation styling begins here...
            .accomodation_parent {
                margin-top: $main-spacing;

                .container {

                    .header {
                        h1 {
                            font-size: clamp(1.8rem, 2.5vw, 2.5rem);
                            margin-bottom: 0;
                        }
                    }

                    .flexbox {
                        flex-direction: column;

                        .flex {
                            width: 100%;

                            &:nth-child(2) {
                                height: 250px;
                                justify-content: center;
                                position: relative;
                                z-index: -1;

                                img {
                                    width: 80%;
                                }
                            }
                        }


                    }

                    .flights_parent {
                        margin-top: 0px;
                    }
                }
            }

            //visa information styling begins here..
            .visa_parent {
                margin-top: $main-spacing;

                .container {

                    .header {
                        h1 {
                            font-size: clamp(1.8rem, 2.5vw, 2.5rem);
                        }
                    }

                    .details {
                        ol {
                            padding-left: 20px;
                        }
                    }

                    .button_holder {
                        button {
                            padding: 10px 10px;
                            font-size: .8rem;

                            &:nth-child(2) {
                                margin: 0;
                            }
                        }
                    }
                }
            }

            //Speakers styling begins here...
            .speakers_parent {
                margin-top: $main-spacing;

                .container {
                    width: 70% !important;

                    .header {
                        h1 {
                            font-size: clamp(1.8rem, 2.5vw, 2.5rem);
                        }
                    }

                    .flexbox {
                        flex-direction: column;

                        .speaker {
                            width: 100%;
                        }
                    }
                }
            }

            //Footer styling begins here...
            footer {
                margin-top: $main-spacing;

                .footer_parent {
                    .container {
                        .flexbox {
                            flex-direction: column;
                            justify-content: flex-start;
                            align-items: flex-start;

                            .flex {
                                &:nth-child(2) {
                                    p {
                                        width: 80%;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

}

//Container queries

@container (width < 1100px) {
    body {
        section {
            .program_parent {

                .container {
                    .program_container {

                        .header{
                            font-size: clamp(1rem, 1.5vw, 1.5rem);
                            
                        }
                        .program_body {

                            .program_calender {
                                .program_flexbox {
                                    .flex {
                                        p {
                                            font-size: .9rem;
                                        }
                                    }

                                    .flex:nth-child(2) {
                                        .line {
                                            height: 50px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}